.App {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(8px + 2vmin);
  color: white;
}

.left-bar {
  position: fixed;
  top: 80px;
  width: 200px;
  padding: 40px;
  height: 100%;
}

.top-container {
  position: fixed;
  height: 80px;
  width: calc(100% - 400px);
  left: 200px;
  text-align: center;
  z-index: 99;
}

.game-container {
  position: fixed;
  width: calc(100% - 320px);
  padding: 100px 160px ;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.best {
  color: cyan;
}

.good {
  color: green;
}

.dud {
  color: red;
}

* {
  cursor: none;
}

.cursor {
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
  will-change: width, height, transform, border;
  z-index: 999;
  pointer-events: none;
}

.selected {
  top: 16;
  left: 16;
  float: left;
}

.gameboard {
  background-color: #181c14;
  width: 800px;
  height: 600px;
}

.grid-wrapper {
  position: relative;
  padding-top: 144px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 440px;
  height: 320px;
  gap: 20px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 48px 20px;
  justify-content: space-between;
}

.target {
  width: 64px;
  height: 64px;  
  overflow: hidden;
}
